* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background: radial-gradient(circle, #051b69 0%, #0a0e1c 100%);
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

nav {
  width: 100%;
  background-color: #020209;
  position: fixed;
  top: 0;
}

nav ul {
  justify-content: center;
  display: flex;
}

nav li {
  border: 1px solid gray;
  padding: 20px;
  list-style: none;
}

nav a {
  color: #0ff;
  font-size: 2rem;
  text-decoration: none;
}

#home, .sec {
  height: 100vh;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

#home h1 {
  width: 70%;
  text-align: center;
  color: #dc14a7;
  text-shadow: 3px 3px 3px #0ff;
  margin: auto;
  font-size: 6rem;
}

#cont {
  height: 600px;
  width: 600px;
  text-align: center;
  background-color: #808080a9;
  border-radius: 10px;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  margin: auto;
  display: flex;
}

.input, .output {
  width: 80%;
  border: none;
  border-radius: 5px;
  padding: 20px;
  font-size: 1.5rem;
}

.output {
  color: #d2c6c6;
  background-color: #0a0e1c;
  font-size: 1.5rem;
}

.btn {
  color: #d2c6c6;
  background-color: #dc14a7;
  border: none;
  border-radius: 10px;
  padding: 16px;
  font-size: 1.5rem;
  box-shadow: 4px 4px 4px #0a0e1c;
}

h3 {
  color: #0ff;
  margin-top: 100px;
  font-size: 3rem;
}

#netSalary {
  color: #000;
  background-color: #0ff;
  font-weight: 500;
}

